import React from 'react'

import { BASE_BACKEND_URL } from '../../../assets/constant';

const DeleteBillboardButton = ({btn_class}) => {

    const BillboardData = JSON.parse(sessionStorage.getItem('billboard'));

    const DeleteBillboard = async () => {
      
        // Using Fetch API
        fetch(`${BASE_BACKEND_URL}api/billboard/delete_billboard`, {
        method: 'DELETE',
        mode: 'cors',
        body: JSON.stringify({
            billboard_id: `${BillboardData.billboard_id}`
        }),
        headers: {
            'Content-type': 'application/json; charset=UTF-8',
            'token': `Bearer ${window.localStorage.getItem('token').toString()}`,
        },
        })
        .then((response) => response.json())
        .then((data) => {
            if(data === "Billboard has been deleted"){
                alert(data);
                window.location.href = "#/dashboard"
            } else{
                alert(data);
            }
        })
        .catch((error) => {
            alert('Some Error Occurred!');
        });
    }

    return (
        <button
            onClick={() => DeleteBillboard()}
            type="button"
            className={btn_class}
            style={{marginLeft: '30%'}}
        >
            Delete Billboard
        </button>
    )
}

export default DeleteBillboardButton