import React from 'react'

import { BASE_BACKEND_URL } from '../assets/constant';

import Logo from '../assets/img/hadplus_logo.png'

const NavBar = () => {
  const NavMobile = () => {
    if(document.getElementById('navbar-solid-bg').classList.contains('hidden')){
      document.getElementById('navbar-solid-bg').classList.remove('hidden');
    } else{
      document.getElementById('navbar-solid-bg').classList.add('hidden');
    }
  }

  const LoggedIn = () => {
    document.getElementById('dashboard-button').classList.remove('hidden');
    document.getElementById('logout-button').classList.remove('hidden');
    document.getElementById('register-button').classList.add('hidden');
    document.getElementById('login-button').classList.add('hidden');
    document.getElementById('register-button').classList.remove('pl-3');
    document.getElementById('login-button').classList.remove('pl-3');
  }

  const LoggedOut = () => {
    document.getElementById('dashboard-button').classList.add('hidden');
    document.getElementById('logout-button').classList.add('hidden');
    document.getElementById('dashboard-button').clFlowBiteassList.remove('pl-3');
    document.getElementById('logout-button').classList.remove('pl-3');
    document.getElementById('register-button').classList.remove('hidden');
    document.getElementById('login-button').classList.remove('hidden');
  }

  const isUserLoggedIn = async () => {
    try{
      const response = await fetch(`${BASE_BACKEND_URL}api/auth/is-login/${window.localStorage.getItem('user_id').toString()}`, {
        method: 'GET',
        mode: 'cors',
        headers: {
          'Content-type': 'application/json; charset=UTF-8',
          'token': `Bearer ${window.localStorage.getItem('token').toString()}`
        },
      });

      if(response.status >= 200 && response.status < 300){
				LoggedIn();
			}else{
        LoggedOut();
			}
    } catch (error) {
      LoggedOut();
    }
  }

  const Logout = () => {
    window.localStorage.removeItem('username');
    window.localStorage.removeItem('token');
    window.location.href='/'
  }

  return (
    <nav onLoad={()=>{isUserLoggedIn()}} className="p-3 border-gray-200 rounded bg-gray-50 dark:bg-gray-800 dark:border-gray-700">
      <div className="container flex flex-wrap items-center justify-between mx-auto">
        <a href="/#/" className="flex items-center">
            <img src={Logo} className="h-7 mr-3 sm:h-10" alt="hadplus Logo" />
            {/* <span className="self-center text-xl font-semibold whitespace-nowrap dark:text-white">IITK Trip</span> */}
        </a>
        <button onClick={()=>{NavMobile()}} data-collapse-toggle="navbar-solid-bg" type="button" className="inline-flex items-center p-2 ml-3 text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600" aria-controls="navbar-solid-bg" aria-expanded="false">
          <span className="sr-only">Open main menu</span>
          <svg className="w-6 h-6" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z" clipRule="evenodd"></path></svg>
        </button>
        <div className="hidden w-full md:block md:w-auto" id="navbar-solid-bg">
          <ul className="flex flex-col mt-4 rounded-lg bg-gray-50 md:flex-row md:space-x-8 md:mt-0 md:text-sm md:font-medium md:border-0 md:bg-transparent dark:bg-gray-800 md:dark:bg-transparent dark:border-gray-700">
            <li>
              <a href="/#/" className="block py-2 pl-3 pr-4 text-white bg-blue-700 rounded md:bg-transparent md:text-blue-700 md:p-0 md:dark:text-white dark:bg-blue-600 md:dark:bg-transparent" aria-current="page">About Us</a>
            </li>
            <li>
              <a href="/#/dashboard" id='dashboard-button' className="hidden block py-2 pl-3 pr-4 text-gray-700 rounded hover:bg-gray-100 md:hover:bg-transparent md:border-0 md:hover:text-blue-700 md:p-0 dark:text-gray-400 md:dark:hover:text-white dark:hover:bg-gray-700 dark:hover:text-white md:dark:hover:bg-transparent">Dashboard</a>
            </li>
            <li>
              <a href="/#/register" id='register-button' className="block py-2 pl-3 pr-4 text-gray-700 rounded hover:bg-gray-100 md:hover:bg-transparent md:border-0 md:hover:text-blue-700 md:p-0 dark:text-gray-400 md:dark:hover:text-white dark:hover:bg-gray-700 dark:hover:text-white md:dark:hover:bg-transparent">Register</a>
            </li>
            <li>
              <button onClick={() => {Logout();}}  id='logout-button' className="hidden block py-2 pl-3 pr-4 text-gray-700 rounded hover:bg-gray-100 md:hover:bg-transparent md:border-0 md:hover:text-blue-700 md:p-0 dark:text-gray-400 md:dark:hover:text-white dark:hover:bg-gray-700 dark:hover:text-white md:dark:hover:bg-transparent">Logout</button>
            </li>
            <li>
              <a href="/#/login" id='login-button' className="block py-2 pl-3 pr-4 text-gray-700 rounded hover:bg-gray-100 md:hover:bg-transparent md:border-0 md:hover:text-blue-700 md:p-0 dark:text-gray-400 md:dark:hover:text-white dark:hover:bg-gray-700 dark:hover:text-white md:dark:hover:bg-transparent">Login</a>
            </li>
            <li>
              <label className="relative inline-flex items-center cursor-pointer">
                <input id="dark-mode-toggle" type="checkbox" value="" className="sr-only peer"/>
                <div className="w-11 h-6 bg-gray-200 rounded-full peer peer-focus:ring-4 peer-focus:ring-blue-300 dark:peer-focus:ring-blue-800 dark:bg-gray-700 peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-0.5 after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all dark:border-gray-600 peer-checked:bg-blue-600"></div>
                <span className="ml-3 text-sm font-medium text-gray-900 dark:text-gray-300">Dark Mode</span>
              </label>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  )
}

const darkModeToggle = document.getElementById("dark-mode-toggle");

if(darkModeToggle != null){

  darkModeToggle.addEventListener("change", function(){

    // if set via local storage previously
    if (window.localStorage.getItem('color-theme')) {
      if (window.localStorage.getItem('color-theme') === 'light') {
          document.documentElement.classList.add('dark');
          window.localStorage.setItem('color-theme', 'dark');
      } else {
          document.documentElement.classList.remove('dark');
          window.localStorage.setItem('color-theme', 'light');
      }

    // if NOT set via local storage previously
    } else {
        if (document.documentElement.classList.contains('dark')) {
            document.documentElement.classList.remove('dark');
            window.localStorage.setItem('color-theme', 'light');
        } else {
            document.documentElement.classList.add('dark');
            window.localStorage.setItem('color-theme', 'dark');
        }
    }
  })
}

export default NavBar