import React, {useState, useEffect} from 'react'
import { BASE_BACKEND_URL } from '../../../assets/constant';

import AddBillboardButton from './AddBillboardButton'

export default function AddBillboardForm() {

  const [RegulatorData, setRegulatorData] = useState([]);

  async function getAllRegulators() {
      fetch(`${BASE_BACKEND_URL}api/users/regulators`, {
          method: 'GET',
          mode: 'cors',
          headers: {
              'Content-type': 'application/json; charset=UTF-8',
              'token': `Bearer ${window.localStorage.getItem('token').toString()}`,
          },
      })
          .then((response) => response.json())
          .then((data) => {
              if(typeof(data)=='object'){
                setRegulatorData(data);
              } else {
                alert('Some Error Occurred!');
              }
          })
          .catch((error) => {
              alert('Some Error Occurred!');
          });
  };

  useEffect(() => {
    getAllRegulators();
  }, [])

  const uploadBillboardImage = (event) => {
    var file = event.target.files[0];
    let fr = new FileReader();
    fr.addEventListener('loadend', () => {
      let res = fr.result;
      let spt = res.split("base64,")[1];
      sessionStorage.setItem('billboard-image',spt);
    })
    fr.readAsDataURL(file)
  }

  return (
    <div className="overflow-hidden bg-white shadow sm:rounded-lg">
      <div className="px-4 py-5 sm:px-6">
        <h3 className="text-base font-semibold leading-6 text-gray-900 flex items-center justify-center">Add Billboard</h3>
      </div>
      <div className="border-t border-gray-200 flex items-center justify-center">
        <dl>
          <div className="bg-gray-50 px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt className="text-sm font-medium text-gray-500">Latitude</dt>
            <input type="text" id="latitude" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="26.510771608145642" required />
          </div>
          <div className="bg-white px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt className="text-sm font-medium text-gray-500">Longitude</dt>
            <input type="text" id="longitude" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="80.23007773798072" required />
          </div>
          <div className="bg-gray-50 px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt className="text-sm font-medium text-gray-500">Height of Billboard (in meters)</dt>
            <input type="text" id="height" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="5" required />
          </div>
          <div className="bg-white px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt className="text-sm font-medium text-gray-500">Width of Billboard (in meters)</dt>
            <input type="text" id="width" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="7" required />
          </div>
          <div className="bg-gray-50 px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt className="text-sm font-medium text-gray-500">Address</dt>
            <input type="text" id="address" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Indian Institute of Technology Kanpur, Hall of Residence II, Sixth Ave, Nankari, Kalyanpur, Kanpur, Uttar Pradesh 208016" required />
          </div>
          <div className="bg-white px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt className="text-sm font-medium text-gray-500">Land Type</dt>
            <input type="text" id="land-type" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Government, Private" required />
          </div>
          <div className="bg-white px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt className="text-sm font-medium text-gray-500">Billboard Type</dt>
            <input type="text" id="billboard-type" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Dark, Lit, Semi Lit, Back Lit" required />
          </div>
          <div className="bg-gray-50 px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt className="text-sm font-medium text-gray-500">Governing Body</dt>
            <select type="text" id="governing-body" className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Solan Municipal Corporation" required >
            {(() => {
                            const arr = [];
                            for (let i = 0; i < RegulatorData.length; i++) {
                                arr.push(
                                    <option value={RegulatorData[i]} key={i}>
                                      {RegulatorData[i]}
                                    </option>
                                );
                            }
                            return arr;
            })()}
            </select>
          </div>
          <div className="bg-white px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
            <dt className="text-sm font-medium text-gray-500">Image of Billboard</dt>
            <input type="file" id="billboard-image" onChange={(event) => uploadBillboardImage(event)} class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Solan Municipal Corporation" required />
          </div>
          <AddBillboardButton 
          btn_class={'bg-[#002D74] rounded-xl text-white px-2 py-2 hover:scale-105 duration-300 mb-16 mt-6'}
          />
        </dl>
      </div>
    </div>
  )
}
