import React from 'react'

const DashboardRow = ( { obj } ) => {
  return (
    <>
        <tr onClick={() => {
            sessionStorage.setItem('billboard', JSON.stringify(obj));
            window.location.href="#/view_billboard"
        }} className="bg-white border-b dark:bg-gray-800 dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-600">
        <td className="px-6 py-4">
                <p className="font-bold text-black dark:text-white hover:underline">{obj.billboard_id}</p>
            </td>
            <td className="px-6 py-4">
                <p className="font-medium text-black dark:text-white hover:underline">{obj.address}</p>
            </td>
            <td className="px-6 py-4">
                <p className="font-medium text-black dark:text-white hover:underline">{obj.latitude}</p>
            </td>
            <td className="px-6 py-4">
                <p className="font-medium text-black dark:text-white hover:underline">{obj.longitude}</p>
            </td>
        </tr>
    </>
  )
}

export default DashboardRow