import { Route, Routes, useHistory } from 'react-router-dom';
import { useState } from 'react';

// import pages
import Register from './pages/register/Register';
import Login from './pages/login/Login';
import ViewBillboard from './pages/view_billboard/ViewBillboard';
import AddBillboard from './pages/add_billboard/AddBillboard';
import Dashboard from './pages/dashboard/Dashboard';

// import animate on scroll
import Aos from 'aos';
import 'aos/dist/aos.css';

import { BASE_BACKEND_URL } from './assets/constant';

function App() {
  const [LoggedIn, SetLoggedIn] = useState(false);

  async function isUserLoggedIn() {
    try{
      const response = await fetch(`${BASE_BACKEND_URL}api/auth/is-login/${window.localStorage.getItem('user_id').toString()}`, {
        method: 'GET',
        mode: 'cors',
        headers: {
          'Content-type': 'application/json; charset=UTF-8',
          'token': `Bearer ${window.localStorage.getItem('token').toString()}`
        },
      });
      if(response.status >= 200 && response.status < 300){
        SetLoggedIn(true);
      } else {
        SetLoggedIn(false);
      }
    } catch(error){
      SetLoggedIn(false);
    }
  }

  // animate on scroll initialization
  Aos.init({
    duration: 450,
    offset: 0,
  });
  return (
    <div onLoad={()=>{isUserLoggedIn()}} className='overflow-hidden'>
        <Routes>
          <Route exact path="/" element={LoggedIn?(<Dashboard />):(<Login />)} />
          <Route exact path="/register" element={<Register />} />
          <Route exact path="/login" element={<Login />} />
          <Route exact path="/dashboard" element={LoggedIn?(<Dashboard />):(<Login />)} />
          <Route exact path="/view_billboard" element={LoggedIn?(<ViewBillboard />):(<Login />)} />
          <Route exact path="/add_billboard" element={LoggedIn?(<AddBillboard />):(<Login />)} />
        </Routes>
        {/* <Footer /> */}
    </div>
  );
}

export default App;