import React, {useState} from 'react'

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

import { BASE_BACKEND_URL, BASE_URL } from '../../../assets/constant';

const CryptoJS = require("crypto-js");

const RegisterButton = ({btn_class}) => {

  function getOTP() {
      
    // Using Fetch API
    fetch(`${BASE_BACKEND_URL}api/auth/send_otp`, {
      method: 'POST',
      mode: 'cors',
      body: JSON.stringify({
        name: document.getElementById("name").value,
        email: document.getElementById("email").value,
        phone_number: document.getElementById("phone_number").value,
        password: CryptoJS.SHA256(document.getElementById("password").value).toString(CryptoJS.enc.Hex),
      }),
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
      },
    })
    .then((response) => response.json())
    .then((data) => {
      handleMessage(data);
      handleShow();
    })
    .catch((error) => {
      handleMessage('Some Error Occurred!');
      handleShow();
    });
  }

  function registerUser() {
    // Using Fetch API
    fetch(`${BASE_BACKEND_URL}api/auth/register`, {
      method: 'POST',
      mode: 'cors',
      body: JSON.stringify({
        email: document.getElementById("email").value,
        otp: document.getElementById('otp-input').value.toString(),
      }),
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
      },
    })
    .then((response) => response.json())
    .then((data) => {
      if(data === 'Congratulations!!! Your account is created successfully! Login to continue futher.'){
        handleClose();
        alert(data);
        window.location.href = `${BASE_URL}/#?login`
      } else {
        alert(data);
        window.location.href = `${BASE_URL}/#/login`
      }
    })
    .catch((error) => {
      handleClose();
        alert("Failed to create your account. Try again.")
    });
  }

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const handleMessage = (message) => document.getElementById('otp-message').innerHTML = message;

  return (
    <>
        <button
          onClick={() => {getOTP(); handleShow();}}
          type="button"
          className={btn_class}
        >
          Register
        </button>

        <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title style={{fontSize: "2.5vh"}}>IITK Trip - Email Verification</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          ENTER YOUR OTP 
          <br /><br />
          <input
            type="text"
            id='otp-input'
            class="form-control block w-full px-4 py-2 mb-4 md:mb-0 md:mr-2 text-xl font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded-lg transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none"
            placeholder="Enter your email"
          />
          <br />
          <span id='otp-message'></span>
          <br /><br />
          <span>Didn't recieved the OTP. <button style={{fontWeight: 'bold'}} onClick={() => getOTP()}>Resend</button></span>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="primary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={registerUser}>
            Submit
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  )
}

export default RegisterButton