import React from 'react'

import { BASE_BACKEND_URL } from '../../../assets/constant';

const AddBillboardButton = ({btn_class}) => {

    const AddBillboard = async () => {
      
        // Using Fetch API
        fetch(`${BASE_BACKEND_URL}api/billboard/add_billboard`, {
        method: 'POST',
        mode: 'cors',
        body: JSON.stringify({
            latitude: document.getElementById("latitude").value,
            longitude: document.getElementById("longitude").value,
            height: document.getElementById("height").value,
            width: document.getElementById("width").value,
            address: document.getElementById("address").value,
            land_type: document.getElementById("land-type").value,
            billboard_type: document.getElementById("billboard-type").value,
            governing_body: document.getElementById("governing-body").value,
            billboard_image: sessionStorage.getItem('billboard-image'),
        }),
        headers: {
            'Content-type': 'application/json; charset=UTF-8',
            'token': `Bearer ${window.localStorage.getItem('token').toString()}`,
        },
        })
        .then((response) => response.json())
        .then((data) => {
        alert(data);
        })
        .catch((error) => {
        alert('Some Error Occurred!');
        });
    }

    return (
        <button
            onClick={() => AddBillboard()}
            type="button"
            className={btn_class}
            style={{marginLeft: '30%'}}
        >
            Add Billboard
        </button>
    )
}

export default AddBillboardButton