import React, {useState} from 'react'
import DashboardRow from './components/DashboardRow'

import { BASE_BACKEND_URL } from '../../assets/constant';

import NavBar from '../../components/NavBar';
import Footer from '../../components/Footer';

const Dashboard = () => {
    const [Count, setCount] = useState(0);

    const [BillboardData, setBillboardData] = useState([]);

    async function getAllBillboards() {
        fetch(`${BASE_BACKEND_URL}api/billboard`, {
            method: 'GET',
            mode: 'cors',
            headers: {
                'Content-type': 'application/json; charset=UTF-8',
                'token': `Bearer ${window.localStorage.getItem('token').toString()}`,
            },
        })
            .then((response) => response.json())
            .then((data) => {
                if(typeof(data)=='object'){
                    setBillboardData(data);
                } else {
                    alert('Some Error Occurred!');
                }
            })
            .catch((error) => {
                alert('Some Error Occurred!');
            });
    };
    
    if(Count===0){
        window.onload = getAllBillboards();
        setCount(1);
    }

    return (
        <>
            <NavBar />
            <div id='dashboard' className="relative overflow-x-auto shadow-md sm:rounded-lg">
                <div className="flex items-center justify-between pb-4 bg-white dark:bg-gray-900">
                    <button
                        onClick={() => {window.location.href="#/add_billboard"}}
                        type="button"
                        className="bg-[#002D74] rounded-xl text-white py-5 px-20 hover:scale-105 duration-300 ml-10"
                    >
                        Add Billboard
                    </button>
                </div>
                <div className="flex items-center justify-between pb-4 bg-white dark:bg-gray-900">
                    <label htmlFor="table-search" className="sr-only">Search</label>
                    <div className="relative">
                        <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
                            <svg className="w-5 h-5 text-gray-500 dark:text-gray-400" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd"></path></svg>
                        </div>
                        <input type="text" id="table-search-users" className="block p-2 pl-10 text-sm text-gray-900 border border-gray-300 rounded-lg w-80 bg-gray-50 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Search for users" />
                    </div>
                </div>
                <table className="min-h-screen w-full text-sm text-left text-gray-500 dark:text-gray-400">
                    <thead className="text-xs text-gray-700 uppercase bg-gray-50 dark:bg-gray-700 dark:text-gray-400">
                        <tr>
                            <th scope="col" className="px-6 py-3">
                                Billboard ID
                            </th>
                            <th scope="col" className="px-6 py-3">
                                Address
                            </th>
                            <th scope="col" className="px-6 py-3">
                                Latitude
                            </th>
                            <th scope="col" className="px-6 py-3">
                                Longitude
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {(() => {
                            const arr = [];
                            for (let i = 0; i < BillboardData.length; i++) {
                                arr.push(
                                    <DashboardRow obj={BillboardData[i]} key={i} />
                                );
                            }
                            return arr;
                        })()}
                    </tbody>
                </table>
            </div>
            <Footer />
        </>
    )
}

export default Dashboard